axios.defaults.headers['Pragma'] ='no-cache';
axios.defaults.headers['Cache-Control'] ='no-cache';
//axios.defaults.headers = "{'Pragma': 'no-cache','Cache-Control': 'no-cache'}";//
//axios.defaults.headers['Content-Type'] = 'multipart/form-data'; //req body
axios.defaults.headers['X-Requested-with'] ='XMLHttpRequest';//`headers`
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; //
//axios.defaults.baseURL = 'http://192.168.2.57:8001';

axios.defaults.headers['Access-Control-Allow-Origin'] ='*';
//axios.defaults.headers['Origin'] ='http://192.168.0.187:8080';
//axios.defaults.headers['Access-Control-Allow-Headers'] ='X-Requested-With,Content-Type';
//axios.defaults.headers['Access-Control-Allow-Methods'] ='PUT,POST,GET,DELETE,OPTIONS';
axios.defaults.headers.common['Content-Type'] = 'application/json;charset=UTF-8';
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
axios.defaults.withCredentials = true;// 

axios.defaults.withCredentials = false;

axios.interceptors.request.use(
    config => {
        /* if (token) {
            config.headers.Authorization = `token123`;
        }*/
        config.headers.common['Access-Control-Allow-Origin'] = '*';
        if (config.method === 'post') {
            config.data = qs.stringify(config.data)
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);

// http response 
